.banner{
  background: url(./assets/images/winners.png);
  background-size: cover;
  width: 100%;
  height: 450px;
  position: relative;
}
.layer{
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 450px;
  background-color: rgba(238, 104, 70, 0.548);
}

.banner .logo-container{
  text-align: center;
  padding-top: 20px;
}
.banner .logo-container img{
  width: 55%;
}

.banner .team-details{
  padding-top: 30px;
  text-align: center;
}
.banner .team-details h3{
  color: #fff;
}

.banner .team-details .venue{
  color: rgb(224, 218, 218);
}

.banner .team-details .venue-value{
  color: #fff;
  margin-left: 20px;
  font-weight: 600;
}




.player-info{
  background-color: #fff;
  color: rgb(20, 20, 20);
  padding: 5px;
  margin: -1px;
  border: 1px solid #d4d3d3;
}
.player-info h6{
  font-weight: 700;
}

.player-image{
  height: 200px;
}

.description-text{
  font-size: 12px;
}

.trophies.not{
  opacity: 0;
}


.sweet-loading{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.player-icon-container{
  position: absolute;
}

.player-icon{
  padding: 0 5px;
}




.col-sm-12 .card{
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
}

.col-sm-12 .card .img-container{
  padding: 10px;
}

.col-sm-12 .card .card-body{
  text-align: left;
}
.col-sm-12 .card .card-body .mobile-name{
  display: inline-block;
  margin-right: 1px;
}

.col-sm-12 .card .card-body .mobile-venue{
  display: none;
}

.col-sm-12 .card .card-body .teams-link{
  float: right;
}


@media screen and (min-width: 960px){
  .layer,
  .banner{
    height: 250px;
  }
  
  .banner .team-details h3{
    text-align: left;
  }
  .col-sm-12 .card{
    flex-direction: column;
  }
  .col-sm-12 .card .img-container{
    padding: 0px;
  }
  
  .col-sm-12 .card .card-body{
    text-align: center;
  }
  .col-sm-12 .card .card-body .mobile-name{
    display: block;
  }
  
  .col-sm-12 .card .card-body .mobile-venue{
    display: block;
  }
  
  .col-sm-12 .card .card-body .teams-link{
    float: none;
  }
}