:root{
  --csk: #F9CD05;
  --ddd: #00008B;
  --kxib: #ED1B24; 
  --kkr:  #2E0854;
  --mi: #004BA0;
  --rr: #254AA5;
  --rcb: #2B2A29;
  --shr: #FF822A;
  --primary-font-color: rgb(224, 214, 214);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, .col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, .col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto{
  padding-left: 3px;
  padding-right: 3px;
  margin-bottom: 10px;
}
.card{
  color: #fff;
}
.trophies{
    background-color: rgba(1,1,1,0.4);
    padding: 5px 10px;
    border-radius: 15px;
    color: gold;
    font-weight: 600;
    font-size: 12px
}

.teams-link{
  font-weight: 600;
  color: #fff;
  font-size: 12px;
  text-decoration: none;
}

.teams-link:hover{
  color: rgb(199, 195, 195);
  text-decoration: none;
}


.card.chennai-super-kings{
  background-image: linear-gradient(175deg, var(--csk), #ffae00);
}

.card.delhi-capitals{
  background-image: linear-gradient(45deg, var(--ddd), #01014d);
}
.card.kings-xi-punjab{
  background-image: linear-gradient(45deg, var(--kxib), #ed1b6b);
}
.card.kolkata-knight-riders{
  background-image: linear-gradient(45deg, var(--kkr), #160854);
}
.card.mumbai-indians{
  background-image: linear-gradient(45deg, var(--mi), #0033a0);
}
.card.rajasthan-royals{
  background-image: linear-gradient(45deg, var(--rr), #0022e4);
}
.card.royal-challengers-bangalore{
  background-image: linear-gradient(45deg, var(--rcb), #1b1a19);
}
.card.sunrisers-hyderabad{
  background-image: linear-gradient(45deg, var(--shr), #ad4902);
}


.layer.chennai-super-kings{
  background-color: rgba(249, 205, 5, .7);
}

.layer.delhi-capitals{
  background-color: rgba(0, 0, 139, .7);
}
.layer.kings-xi-punjab{
  background-color: rgba(237, 27, 36, .7);
}
.layer.kolkata-knight-riders{
  background-color: rgba(46, 8, 84, .7);
}
.layer.mumbai-indians{
  background-color: rgba(0, 75, 160, .7);
}
.layer.rajasthan-royals{
  background-color: rgba(37, 74, 165, .7);
}
.layer.royal-challengers-bangalore{
  background-color: rgba(43, 42, 41, .7);
}
.layer.sunrisers-hyderabad{
  background-color: rgba(255, 130, 42, .7);
}